import React, { useEffect, useState } from 'react';
import PropTypes, { number } from 'prop-types';
import { Enums, Types, utilities } from '@cornerstonejs/core';
import { utilities as csToolsUtils } from '@cornerstonejs/tools';
import { ImageScrollbar } from '@ohif/ui';

function CornerstoneImageScrollbar({
  viewportData,
  viewportId,
  element,
  imageSliceData,
  setImageSliceData,
  scrollbarWidth,
  servicesManager,
}: withAppTypes) {
  const {
    cineService,
    cornerstoneViewportService,
    viewportGridService,
    panoramicService,
    hangingProtocolService
  } = servicesManager.services;

  const [isDentascan, setIsDentascan] = useState(false);

  const onImageScrollbarChange = (imageIndex, viewportId) => {
    const viewport = cornerstoneViewportService.getCornerstoneViewport(viewportId);

    const { isCineEnabled } = cineService.getState();

    if (isCineEnabled) {
      // on image scrollbar change, stop the CINE if it is playing
      cineService.stopClip(element, { viewportId });
      cineService.setCine({ id: viewportId, isPlaying: false });
    }

    csToolsUtils.jumpToSlice(viewport.element, { imageIndex, debounceLoading: true, });

    if (hangingProtocolService.getActiveProtocol().protocol.id === 'panoramicViewport') {
      // Hardcoded for scrollbar in panoramic
      const viewportIds = cornerstoneViewportService.getViewportIds();
      const viewportIndex = viewportIds.findIndex(vpId => vpId === viewportId);
      const displaySetUID = viewportGridService.getDisplaySetsUIDsForViewport('default')[0];
      if (viewportIndex >= 2) {
        const middleDentascanViewportIndex = Math.round(viewportIds.length / 2);
        imageIndex += (viewportIndex - middleDentascanViewportIndex) * 2;
        // Disable scrollbar display
        setIsDentascan(true);
      }
      if (viewportIndex >= 1) {
        // Panoramic or dentascan viewport
        panoramicService.setDentascanIndex(displaySetUID, imageIndex);
        setImageSliceData({ imageIndex: imageIndex, numberOfSlices: imageSliceData.numberOfSlices, });
      }
    }
  };

  useEffect(() => {
    if (!viewportData) {
      return;
    }

    const viewport = cornerstoneViewportService.getCornerstoneViewport(viewportId);

    if (!viewport) {
      return;
    }

    if (hangingProtocolService.getActiveProtocol().protocol.id === 'panoramicViewport') {
      // Determine if viewport is dentascan based on viewport position
      const viewportIndex = cornerstoneViewportService.getViewportIds().findIndex(vpId => vpId === viewportId);
      setIsDentascan(viewportIndex >= 2);
    }

    if (viewportData.viewportType === Enums.ViewportType.STACK) {
      const numberOfSlices = (viewport as Types.IStackViewport).getCornerstoneImage().columns;

      setImageSliceData({
        imageIndex: Math.round(numberOfSlices / 2),
        numberOfSlices: numberOfSlices,
      });

      return;
    }

    if (viewportData.viewportType === Enums.ViewportType.ORTHOGRAPHIC) {
      const sliceData = utilities.getImageSliceDataForVolumeViewport(
        viewport as Types.IVolumeViewport
      );

      if (!sliceData) {
        return;
      }

      const { imageIndex, numberOfSlices } = sliceData;
      setImageSliceData({ imageIndex, numberOfSlices });
    }
  }, [viewportId, viewportData]);

  useEffect(() => {
    if (viewportData?.viewportType !== Enums.ViewportType.STACK
      && viewportData?.viewportType !== Enums.ViewportType.ORTHOGRAPHIC) {
      return;
    }

    const updateStackIndex = event => {
      const { newImageIdIndex } = event.detail;
      // find the index of imageId in the imageIds
      setImageSliceData({
        imageIndex: newImageIdIndex,
        numberOfSlices: viewportData.data[0].imageIds.length,
      });
    };

    element.addEventListener(Enums.Events.STACK_VIEWPORT_SCROLL, updateStackIndex);

    return () => {
      element.removeEventListener(Enums.Events.STACK_VIEWPORT_SCROLL, updateStackIndex);
    };
  }, [viewportData, element]);

  useEffect(() => {
    if (viewportData?.viewportType !== Enums.ViewportType.ORTHOGRAPHIC) {
      return;
    }

    const updateVolumeIndex = event => {
      const { imageIndex, numberOfSlices } = event.detail;
      // find the index of imageId in the imageIds
      setImageSliceData({ imageIndex, numberOfSlices });
    };

    element.addEventListener(Enums.Events.VOLUME_NEW_IMAGE, updateVolumeIndex);

    return () => {
      element.removeEventListener(Enums.Events.VOLUME_NEW_IMAGE, updateVolumeIndex);
    };
  }, [viewportData, element]);

  return (
    !isDentascan && <ImageScrollbar
      onChange={evt => onImageScrollbarChange(evt, viewportId)}
      max={imageSliceData.numberOfSlices ? imageSliceData.numberOfSlices - 1 : 0}
      height={scrollbarWidth}
      value={imageSliceData.imageIndex}
    />
  );
}

CornerstoneImageScrollbar.propTypes = {
  viewportData: PropTypes.object,
  viewportId: PropTypes.string.isRequired,
  element: PropTypes.instanceOf(Element),
  scrollbarWidth: PropTypes.string,
  imageSliceData: PropTypes.object.isRequired,
  setImageSliceData: PropTypes.func.isRequired,
  servicesManager: PropTypes.object.isRequired,
};

export default CornerstoneImageScrollbar;
